export type Notification = {
  origin: 'selected' | 'received';
  data: any;
  remote: boolean;
  isMultiple: boolean;
};

export type LocalNotification = {
  title: string;
  // How should we deal with body being required on iOS but not on Android?
  body?: string;
  data?: any;
  categoryId?: string;
  ios?: {
    sound?: boolean;
  };
  android?: {
    channelId?: string;
    icon?: string;
    color?: string;
    sticky?: boolean;
    link?: string;
  };
  web?: NotificationOptions;
};

export type Channel = {
  name: string;
  description?: string;
  priority?: string;
  sound?: boolean;
  vibrate?: boolean | number[];
  badge?: boolean;
};

export type ActionType = {
  actionId: string;
  buttonTitle: string;
  isDestructive?: boolean;
  isAuthenticationRequired?: boolean;
  textInput?: {
    submitButtonTitle: string;
    placeholder: string;
  };
};

// Android assigns unique number to each notification natively.
// Since that's not supported on iOS, we generate an unique string.
export type LocalNotificationId = string | number;
